.App{
    width: 90vw;
    min-height: calc(100vh - 10vw);
    background-color: rgb(227, 227, 227);
    padding: 5vw;

    display: flex;
    flex-direction: column;
    gap: 5vw;
}

.buttonsAddReset{
    display: flex;
    gap: 5px;
}

.ButtonAdd{
    width: 80%;
    height: 50px;
    border: 3px green solid;
    border-radius: 10px 0 0 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 24px;
}

.plusBarreH{
    background-color: green;
    width: 30px;
    height: 5px;
    position: absolute;
    border-radius: 20px;
}

.plusBarreV{
    background-color: green;
    width: 30px;
    height: 5px;
    position: absolute;
    rotate: 90deg;
    border-radius: 20px;
}

.ButtonDelete{
    display: flex;
    align-items: center;
    justify-content: center;

    width: 20%;
    height: 50px;
    border: 3px blue solid;
}

.ButtonDelete .plusBarreH{
    background-color: blue;
    width: 20px;
    height: 5px;
    position: absolute;
    border-radius: 20px;
}

.ButtonReset{
    display: flex;
    align-items: center;
    justify-content: center;

    width: 20%;
    height: 50px;
    border-radius: 0 10px 10px 0;
    border: 3px red solid;
}

.ButtonReset img{
    height: 60%;
}