.ToDoList{
    display: flex;
    flex-direction: column;
    gap: 30px;
}

.Step{
    display: flex;
    flex-direction: column;
    gap: 8px;
}

.task{
    margin-left: 30px;
    max-width: 100%;
    height: max-content;
    background-color: rgb(238, 238, 238);
    padding: 10px 20px;
    border-radius: 10px;
    font-size: 18px;
}

.task p{
    margin: 0px;
}

.CheckPoint{
    width: 100%;
    height: 30px;
    display: flex;
    align-items: center;
    gap: 5px;
}

.CheckPoint p{
    font-size: 20px;
    font-weight: bold;
}

.pointCheckPoint{
    width: 20px;
    height: 20px;
    background-color: inherit;
    border-radius: 50%;
    border: 5px black solid;
    z-index: 1;
}

.ligneCheckPoint{
    width: 20px;
    height: 8px;
    background-color: black;
    border-radius: 20px;
    position: relative;
    right: 10px;
}